import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { ICourseCategoryModel, ICourseCategoryRequest } from "../models/course-category";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class CourseCategoryService extends AppClientService {
	constructor() {
		super();
	}

	create(data: ICourseCategoryRequest) {
		return this.post<IResponse<ICourseCategoryModel>>("TrainingSetup/CourseCategory/Create", data);
	}

	update(data: ICourseCategoryRequest) {
		return this.post<IResponse<ICourseCategoryModel>>("TrainingSetup/CourseCategory/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<ICourseCategoryModel[]>>("TrainingSetup/CourseCategory/GetList", {
			params: params,
		});
	}

	getAll() {
		this.get<IResponse<ICourseCategoryModel[]>>("TrainingSetup/CourseCategory/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("TrainingSetup/CourseCategory/Delete", {
			params: { Id: id },
		});
	}
}
