import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { ICourseTypeModel, ICourseTypeRequest } from "../models/course-type";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class CourseTypeService extends AppClientService {
	constructor() {
		super();
	}

	create(data: ICourseTypeRequest) {
		return this.post<IResponse<ICourseTypeModel>>("TrainingSetup/CourseType/Create", data);
	}

	update(data: ICourseTypeRequest) {
		return this.post<IResponse<ICourseTypeModel>>("TrainingSetup/CourseType/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<ICourseTypeModel[]>>("TrainingSetup/CourseType/GetList", {
			params: params,
		});
	}

	getAll() {
		this.get<IResponse<ICourseTypeModel[]>>("TrainingSetup/CourseType/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("TrainingSetup/CourseType/Delete", {
			params: { Id: id },
		});
	}
}
